@font-face {
  font-family: 'icomoon';
  src:  url('../assets/fonts/icomoon.eot?wdl57y');
  src:  url('../assets/fonts/icomoon.eot?wdl57y#iefix') format('embedded-opentype'),
    url('../assets/fonts/icomoon.ttf?wdl57y') format('truetype'),
    url('../assets/fonts/icomoon.woff?wdl57y') format('woff'),
    url('../assets/fonts/icomoon.svg?wdl57y#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

